@import "../../../sass/consts";

.collection_detail_page {
  background-color: $bg8;

  > .am-flexbox-item {
    overflow-y: auto;
  }

  .base_infos {
    padding: 0 .3rem;
    text-align: center;
    margin-bottom: .5rem;

    .name {
      font-weight: 600;
      font-size: 1.2rem;
    }

    .museum_name {
      padding: .5rem;
      color: $bg7;

      img {
        height: 2rem;
        vertical-align: middle;
        margin-right: .5rem;
      }
    }

    .actions {
      justify-content: center;

      img {
        height: 1rem;
        margin-right: .3rem;
        vertical-align: middle;
      }

      div {
        font-size: .8rem;
        line-height: 2rem;
        border-radius: 1rem;
        padding: 0 .8rem;
        background-color: $bg4;
        color: white;
        margin: 0 .3rem;

        &.active {
          color: #ff6000;
        }
      }
    }

    .describe {
      font-size: .8rem;
      color: #666;
      text-align: left;
      padding: .5rem 0;
    }
  }

  .oprs {
    justify-content: flex-end;
    padding: .5rem 2%;
    font-size: .8rem;

    .opr {
      text-align: center;
      width: 32%;
      color: white;

      img {
        width: 2rem;
      }
    }
  }
}

.collection_comment {
  .state2 {
    padding: .2rem 1rem;
    border-radius: .5rem;
  }
}
